import React, { useState } from "react";
import { Grid, Container } from "@material-ui/core";
import { styled } from "@material-ui/core/styles";
import FlowerItem from "./Components/FlowerItem";

import topImg1 from "../../assets/product/top-bar-1.png";
import topImg2 from "../../assets/product/top-bar-2.png";
import topImg3 from "../../assets/product/top-bar-3.png";

import data from "./Data.js";
import "./index.css";
import "./Components/Flower.css";

function Index() {
    const [category, setCategory] = useState(0);
    return (
        <MyContainer>
            {/* Top Bar Start */}
            <Grid container alignItems="center" justify="center">
                {topBarData.map((item, idx) => {
                    return (
                        <Grid
                            onClick={() => setCategory(idx)}
                            item
                            key={idx}
                            style={
                                idx === category ? TopBarActive : TopBarInactive
                            }
                            className="header"
                        >
                            <img
                                alt="flower menu"
                                src={item}
                                style={{ width: `60%`, objectFit: "contain" }}
                            />
                        </Grid>
                    );
                })}
            </Grid>
            {/* Top Bar End */}
            <div className="flower-container">
                {data.map((item, idx) => {
                    return <FlowerItem data={item} key={idx} />;
                })}
            </div>
        </MyContainer>
    );
}

export default Index;

const MyContainer = styled(Container)({
    minWidth: "300px",
    width: `80%`,
    justifyContent: `center`,
    marginTop: `20px`,
    marginBottom: `20px`,
});

const TopBarActive = {
    backgroundColor: "#fff",
    boxShadow: "10px 10px 20px -10px rgba(0,0,0,0.7)",
    zIndex: 100,
    transform: "scale(1.05)",
};

const TopBarInactive = {
    backgroundColor: "#cff6e9",
};

const topBarData = [topImg1, topImg2, topImg3];
